<template>
  <div class="container">
    <customSelect
      v-model="localSelectedOption"
      :options="options"
    ></customSelect>

    <!-- Орбита в 3D -->
    <div
      v-if="localSelectedOption === 'Орбита в 3D'"
      class="component"
    >
      <h3>Начальные координаты орбиты</h3>
      <div class="coordinates">
        <label for="x">X (км)</label>
        <input
          v-model="localInputs.x"
          id="x"
          type="number"
          step="any"
          @input="validateInput($event, 'x')"
          @blur="emitCoordinate('x')"
        />
        <label for="z">Z (км)</label>
        <input
          v-model="localInputs.z"
          id="z"
          type="number"
          step="any"
          @input="validateInput($event, 'z')"
          @blur="emitCoordinate('z')"
        />
      </div>
    </div>

    <!-- Проекции орбиты -->
    <div
      v-if="localSelectedOption === 'Проекции орбиты'"
      class="component"
    >
      <h3>Проекции орбиты</h3>
      <div class="coordinates">
        <label for="x">X (км)</label>
        <input
          v-model="localInputs.x"
          id="x"
          type="number"
          step="any"
          @input="validateInput($event, 'x')"
          @blur="emitCoordinate('x')"
        />
        <label for="z">Z (км)</label>
        <input
          v-model="localInputs.z"
          id="z"
          type="number"
          step="any"
          @input="validateInput($event, 'z')"
          @blur="emitCoordinate('z')"
        />
      </div>
    </div>

    <!-- График изменения параметра в семействе -->
    <div
      v-if="
        localSelectedOption ===
        'График изменения параметра в семействе'
      "
      class="component"
    >
      <h3>График изменения параметра в семействе</h3>
      <label for="family">Выберите семейство</label>
      <customSelect
        v-model="selectedFamilyLabel"
        :options="familyLabels"
      />

      <label for="paramX">Параметр по оси X</label>
      <customSelect v-model="paramX" :options="paramOptions" />
      <label for="paramY">Параметр по оси Y</label>
      <customSelect v-model="paramY" :options="paramOptions" />
    </div>

    <!-- Сечение Пуанкаре для одной орбиты -->
    <div
      v-if="localSelectedOption === 'Сечение Пуанкаре одной орбиты'"
      class="component"
    >
      <h3>Сечение Пуанкаре одной орбиты</h3>
      <div class="coordinates">
        <label for="x">X (км)</label>
        <input
          v-model="localInputs.x"
          id="x"
          type="number"
          step="any"
          @input="validateInput($event, 'x')"
          @blur="emitCoordinate('x')"
        />
        <label for="z">Z (км)</label>
        <input
          v-model="localInputs.z"
          id="z"
          type="number"
          step="any"
          @input="validateInput($event, 'z')"
          @blur="emitCoordinate('z')"
        />
      </div>
      <label for="plane">Выберите плоскость</label>
      <customSelect v-model="poincarePlane" :options="planeOptions" />
    </div>

    <!-- Сечение Пуанкаре с константой Якоби -->
    <div
      v-if="
        localSelectedOption === 'Сечение Пуанкаре с константой Якоби'
      "
      class="component"
    >
      <h3>Сечение Пуанкаре с константой Якоби</h3>
      <label for="librationPoint">Выберите точку либрации</label>
      <customSelect
        v-model="librationPoint"
        :options="librationPointOptions"
      />
      <label for="plane">Выберите плоскость</label>
      <customSelect v-model="poincarePlane" :options="planeOptions" />
      <label for="jacobiConstant">Константа Якоби (C)</label>
      <input
        v-model.number="jacobiConstantLocal"
        type="number"
        step="any"
      />
      <label for="error">Погрешность (d)</label>
      <input
        v-model.number="jacobiError"
        type="number"
        step="0.00001"
      />
    </div>

    <button @click="buildGraph">Построить</button>
  </div>
</template>

<script>
import customSelect from "@/components/mainPage/customSelect";

export default {
  components: { customSelect },
  props: {
    orbitCoordinates: {
      type: Object,
      required: true,
    },
    selectedOption: {
      type: String,
      required: true,
    },
    jacobiConstant: {
      type: Number,
      default: null,
    },
  },
  emits: [
    "update:selectedOption",
    "update-orbit-coordinates",
    "find-nearest-orbit",
    "update-poincare-plane",
    "build-poincare-section-with-cj",
    "build-parameter-change-graph",
  ],
  data() {
    return {
      localSelectedOption: this.selectedOption,
      localInputs: {
        x: this.formatNumber(this.orbitCoordinates.x),
        y: this.formatNumber(this.orbitCoordinates.y),
        z: this.formatNumber(this.orbitCoordinates.z),
        vx: this.formatNumber(this.orbitCoordinates.vx),
        vy: this.formatNumber(this.orbitCoordinates.vy),
        vz: this.formatNumber(this.orbitCoordinates.vz),
      },
      paramX: null,
      paramY: null,
      poincarePlane: this.$store.state.poincarePlane || null, // Получаем выбранную плоскость
      jacobiConstantLocal: this.jacobiConstant ?? 1,
      jacobiError: 1,
      options: [
        "Орбита в 3D",
        "Проекции орбиты",
        "График изменения параметра в семействе",
        "Сечение Пуанкаре одной орбиты",
        "Сечение Пуанкаре с константой Якоби",
      ],
      paramOptions: [
        "Период",
        "Амплитуда по X",
        "Амплитуда по Y",
        "Амплитуда по Z",
        "Расстояние до Земли",
        "Расстояние до Луны",
        "Константа Якоби",
        "Максимальный множитель Флоке",
      ],
      planeOptions: [
        "x = 0",
        "y = 0",
        "z = 0",
        "vx = 0",
        "vy = 0",
        "vz = 0",
      ],
      librationPoint: "L1",
      librationPointOptions: ["L1", "L2"],
      family: null,
      selectedFamilyLabel: null,
      familyOptions: [
        {
          label: "Горизонтальные орбиты Ляпунова, L1",
          value: "L1.L",
        },
        {
          label: "Горизонтальные орбиты Ляпунова, L2",
          value: "L2.L",
        },
        { label: "Вертикальные орбиты Ляпунова, L1", value: "L1.V" },
        { label: "Вертикальные орбиты Ляпунова, L2", value: "L2.V" },
        { label: "Аксиальные орбиты, L1", value: "L1.A" },
        { label: "Аксиальные орбиты, L2", value: "L2.A" },
        { label: "Гало орбиты, L1", value: "L1.H" },
        { label: "Гало орбиты, L2", value: "L2.H" },
        {
          label: "Двухпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.2P1",
        },
        {
          label: "Двухпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.2P1",
        },
        {
          label: "Трехпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.3P1",
        },
        {
          label: "Трехпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.3P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.4P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.4P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L1 (2P1.2P1)",
          value: "L1.L.2P1.2P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L2 (2P1.2P1)",
          value: "L2.L.2P1.2P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L1 (2P1.3P1)",
          value: "L1.L.2P1.3P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L1 (2P1.3P2)",
          value: "L1.L.2P1.3P2",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L2 (2P1.3P1)",
          value: "L2.L.2P1.3P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L2 (2P1.3P2)",
          value: "L2.L.2P1.3P2",
        },
        {
          label:
            "Двухпериодические квазигоризонтальные орбиты, L1 (2P1.Tan)",
          value: "L1.L.2P1.Tan",
        },
        {
          label:
            "Двухпериодические квазигоризонтальные орбиты, L2 (2P1.Tan)",
          value: "L2.L.2P1.Tan",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P1)",
          value: "L1.H.2P1",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P2)",
          value: "L1.H.2P2",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P3)",
          value: "L1.H.2P3",
        },
        {
          label: "Двухпериодические квазигало орбиты, L2 (2P1)",
          value: "L2.H.2P1",
        },
        {
          label: "Двухпериодические квазигало орбиты, L2 (2P2)",
          value: "L2.H.2P2",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P1)",
          value: "L1.H.3P1",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P2)",
          value: "L1.H.3P2",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P3)",
          value: "L1.H.3P3",
        },
        {
          label: "Трехпериодические квазигало орбиты, L2 (3P1)",
          value: "L2.H.3P1",
        },
        { label: "Квазипериодические орбиты, L1", value: "L1.Q" },
      ],
    };
  },
  methods: {
    // Форматирование числа в строку с фиксированным количеством знаков
    formatNumber(number, decimals = 2) {
      if (number === null || number === undefined) return "0";
      const fixed = Number(number).toFixed(decimals);
      // Удаление лишних нулей в конце
      return fixed.replace(/\.?0+$/, "");
    },
    // Преобразование строки в число с проверкой
    parseCoordinate(value) {
      const parsed = parseFloat(value);
      return isNaN(parsed) ? 0 : parsed;
    },
    // Валидация ввода и предотвращение ввода символа 'e'
    validateInput(event, field) {
      const value = event.target.value;
      // Удаление всех символов кроме цифр, точки и минуса
      const sanitizedValue = value.replace(/[^0-9.-]/g, "");
      if (sanitizedValue !== value) {
        this.localInputs[field] = sanitizedValue;
      }
    },
    // Эмит координаты при потере фокуса
    emitCoordinate(field) {
      const newValue = this.parseCoordinate(this.localInputs[field]);
      const currentValue = this.orbitCoordinates[field];
      if (newValue !== currentValue) {
        this.$emit("update-orbit-coordinates", {
          ...this.orbitCoordinates,
          [field]: newValue,
        });
      }
    },
    // Метод построения графика
    async buildGraph() {
      // Эмитим изменение выбранной опции
      this.$emit("update:selectedOption", this.localSelectedOption);

      // Проверяем выбранную опцию и выполняем соответствующие действия
      if (
        this.localSelectedOption === "Сечение Пуанкаре одной орбиты"
      ) {
        // Эмитим событие для поиска ближайшей орбиты
        this.$emit("find-nearest-orbit");
        // Эмитим событие для обновления выбранной плоскости
        this.$emit("update-poincare-plane", this.poincarePlane);
      } else if (
        this.localSelectedOption === "Орбита в 3D" ||
        this.localSelectedOption === "Проекции орбиты"
      ) {
        // Эмитим событие для поиска ближайшей орбиты
        this.$emit("find-nearest-orbit");
      } else if (
        this.localSelectedOption ===
        "Сечение Пуанкаре с константой Якоби"
      ) {
        // Логика для сечения Пуанкаре с константой Якоби
        console.log(
          "Построение сечения Пуанкаре с константой Якоби:",
          this.jacobiConstantLocal
        );
        this.$emit("build-poincare-section-with-cj", {
          librationPoint: this.librationPoint,
          cj: this.jacobiConstantLocal,
          d: this.jacobiError,
          plane: this.poincarePlane,
        });
      } else if (
        this.localSelectedOption ===
        "График изменения параметра в семействе"
      ) {
        if (
          !this.selectedFamilyLabel ||
          !this.paramX ||
          !this.paramY
        ) {
          alert(
            "Пожалуйста, выберите семейство и параметры для осей X и Y"
          );
          return;
        }
        // Получаем соответствующий value для выбранного label
        const familyValue =
          this.labelToValueMap[this.selectedFamilyLabel];

        // Эмитим событие для построения графика изменения параметра
        this.$emit("build-parameter-change-graph", {
          family: familyValue,
          paramX: this.paramX,
          paramY: this.paramY,
        });
      } else {
        console.log("Неизвестная опция:", this.localSelectedOption);
      }
    },
  },
  watch: {
    selectedOption(newVal) {
      this.localSelectedOption = newVal;
    },
    localSelectedOption(newVal) {
      this.$emit("update:selectedOption", newVal);
    },
    orbitCoordinates: {
      handler(newVal) {
        console.log("orbitCoordinates изменились:", newVal);
        this.localInputs.x = this.formatNumber(newVal.x);
        this.localInputs.z = this.formatNumber(newVal.z);
      },
      deep: true,
    },
    jacobiConstant(newVal) {
      this.jacobiConstantLocal = newVal;
    },
  },
  computed: {
    familyLabels() {
      return this.familyOptions.map((option) => option.label);
    },
    labelToValueMap() {
      return this.familyOptions.reduce((map, option) => {
        map[option.label] = option.value;
        return map;
      }, {});
    },
  },
};
</script>

<style scoped>
.container {
  padding: 48px 43px;
  box-shadow: 0 2px 12px 2px rgba(145, 158, 225, 0.18),
    0 1px 3px 0px rgba(145, 158, 225, 0.14),
    0 0 3px -1px rgba(145, 158, 225, 0.25);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.component {
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.coordinates {
  display: grid;
  grid-template-columns: repeat(6, auto);
  gap: 10px;
  align-items: center;
}

input {
  padding: 5px;
  border: 1.5px solid var(--main);
  border-radius: 5px;
  width: 160px;
}

button {
  padding: 12px 24px;
  background: #e1ebff;
  border-radius: 10px;
  font-size: 24px;
  font-weight: 400;
  color: #2a6ced;
  border: none;
}
button:hover {
  background: #2a6ced;
  color: #e1ebff;
}
h3 {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 25px;
  color: var(--main);
}
p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  color: var(--main);
}
</style>
