<!-- PoincareSectionWithCj.vue -->
<template>
  <div class="container">
    <!-- Loading indicator -->
    <div v-if="isLoading" class="loading-message">
      <div class="spinner"></div>
      <p>Загрузка данных...</p>
    </div>
    <!-- Error message -->
    <div v-else-if="errorMessage" class="error-message">
      <p>{{ errorMessage }}</p>
    </div>
    <!-- Content -->
    <div v-else class="content">
      <h3>Сечение Пуанкаре с константой Якоби</h3>
      <!-- Используем обновленный компонент -->
      <GrapfForCj :dataPoints="dataPoints" :plane="plane" />
    </div>
  </div>
</template>

<script>
import GrapfForCj from "./GrapfForCj.vue";

export default {
  components: {
    GrapfForCj,
  },
  props: {
    librationPoint: {
      type: String,
      required: true,
    },
    cj: {
      type: Number,
      default: 1, // Значение по умолчанию
    },
    d: {
      type: Number,
      default: 1, // Значение по умолчанию
    },
    plane: {
      type: String,
      default: "x = 0", // Значение по умолчанию
    },
  },
  data() {
    return {
      isLoading: false,
      errorMessage: "",
      dataPoints: [],
    };
  },
  computed: {
    fetchParams() {
      return {
        librationPoint: this.librationPoint,
        cj: this.cj,
        d: this.d,
        plane: this.plane,
      };
    },
  },
  watch: {
    fetchParams: {
      handler() {
        this.fetchData();
      },
      deep: true,
      immediate: true, // Вызывать при монтировании
    },
  },
  methods: {
    async fetchData() {
      this.isLoading = true;
      this.errorMessage = "";
      this.dataPoints = [];

      try {
        const response = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/poincare_sections/cj?libration_point=${encodeURIComponent(
            this.librationPoint
          )}&cj=${encodeURIComponent(
            this.cj || 1
          )}&d=${encodeURIComponent(
            this.d || 1
          )}&plane=${encodeURIComponent(this.plane || "x = 0")}`,
          {
            method: "GET",
          }
        );

        if (!response.ok) {
          throw new Error("Ошибка при загрузке данных");
        }

        const data = await response.json();

        this.dataPoints = data || [];
      } catch (error) {
        console.error("Error fetching data:", error);
        this.errorMessage = error.message;
      } finally {
        this.isLoading = false;
      }
    },
  },
};
</script>

